export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './hide.html';\n\nexport default class ExampleLayoutHide extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n\n.demo {\n  :global {\n    .md-layout-item {\n      height: 40px;\n      margin-top: 8px;\n      margin-bottom: 8px;\n\n      span {\n        width: 100%;\n        height: 100%;\n        padding: 8px;\n        display: block;\n        background: md-get-palette-color(teal, 200);\n      }\n    }\n  }\n}\n","html":"<!-- import style from './hide.scss' -->\n<div :class=\"[style.demo, 'md-layout md-gutter md-alignment-center']\">\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\">\n    <span>Always Show</span>\n  </div>\n\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\">\n    <span>Always Show</span>\n  </div>\n\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\">\n    <span>Always Show</span>\n  </div>\n\n  <div class=\"md-layout-item md-medium-size-50 md-small-size-50 md-xsmall-hide\">\n    <span>Hide Xsmall</span>\n  </div>\n\n  <div class=\"md-layout-item md-medium-size-50 md-small-hide\">\n    <span>Hide Small</span>\n  </div>\n\n  <div class=\"md-layout-item md-medium-hide\">\n    <span>Hide Medium</span>\n  </div>\n</div>"}