export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './alignment.html';\n\nexport default class ExampleLayoutAlignment extends Component {\n  static template = _tpl;\n\n  vertical: string;\n  horizontal: string;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.vertical = null;\n    this.horizontal = null;\n  }\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n@import '~jinge-material/style/animation/variables.scss';\n\n.demo {\n  :global {\n    .md-layout {\n      transition: 0.3s $md-transition-stand-timing;\n    }\n\n    .example .md-layout-item {\n      min-height: 40px;\n      transition: 0.3s $md-transition-stand-timing;\n\n      span {\n        width: 100%;\n        height: 100%;\n        padding: 8px;\n        display: block;\n        background: md-get-palette-color(blue, 200);\n      }\n    }\n  }\n}\n","html":"<!-- import style from './alignment.scss' -->\n<div :class=\"style.demo\">\n  <div class=\"md-layout md-gutter\">\n    <div class=\"md-layout-item md-size-25\">\n      <md-field>\n        <label for=\"vertical\">Vertical</label>\n        <md-select id=\"vertical\" e:value=\"vertical\" on:change=\"vertical = $args[0]\">\n          <md-option value=\"top\">Top</md-option>\n          <md-option value=\"center\">Center</md-option>\n          <md-option value=\"bottom\">Bottom</md-option>\n        </md-select>\n      </md-field>\n    </div>\n\n    <div class=\"md-layout-item md-size-25\">\n      <md-field>\n        <label for=\"horizontal\">Horizontal</label>\n        <md-select id=\"horizontal\" e:value=\"horizontal\" on:change=\"horizontal = $args[0]\" >\n          <md-option value=\"left\">Left</md-option>\n          <md-option value=\"center\">Center</md-option>\n          <md-option value=\"right\">Right</md-option>\n          <md-option value=\"space-around\">Space Around</md-option>\n          <md-option value=\"space-between\">Space Between</md-option>\n        </md-select>\n      </md-field>\n    </div>\n  </div>\n\n  <div class=\"example\">\n    <div class=\"md-layout md-gutter md-alignment-${vertical}-${horizontal}\">\n      <div class=\"md-layout-item md-size-25\">\n        <span>Lorem ipsum dolor sit amet.</span>\n      </div>\n\n      <div class=\"md-layout-item md-size-25\">\n        <span>Repellat praesentium quasi ipsa totam, delectus aperiam deleniti, voluptates inventore.</span>\n      </div>\n\n      <div class=\"md-layout-item md-size-25\">\n        <span>Excepturi natus dolorem maxime.</span>\n      </div>\n    </div>\n  </div>\n</div>"}