export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './nested.html';\n\nexport default class ExampleLayoutNested extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n\n.demo {\n  :global {\n    .md-layout-item {\n      height: 72px;\n\n      &::after {\n        width: 100%;\n        height: 100%;\n        display: block;\n        content: ' ';\n      }\n\n      &.md-layout {\n        &::after {\n          transform: translateY(-100%);\n          background: md-get-palette-color(yellow, 200);\n        }\n\n        .md-layout-item::after {\n          height: 40px;\n          margin-top: 16px;\n          position: relative;\n          z-index: 1;\n          background: md-get-palette-color(pink, 200);\n        }\n      }\n    }\n  }\n}\n","html":"<!-- import style from './nested.scss' -->\n<div :class=\"style.demo\">\n  <div class=\"md-layout md-gutter\">\n    <div class=\"md-layout-item md-layout md-gutter\">\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n    </div>\n\n    <div class=\"md-layout-item md-layout md-gutter\">\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n    </div>\n\n    <div class=\"md-layout-item md-layout md-gutter\">\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n      <div class=\"md-layout-item\"></div>\n    </div>\n  </div>\n</div>"}