export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './columns.html';\n\nexport default class ExampleLayoutColumns extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n\n.demo {\n  :global {\n    .md-layout-item {\n      height: 40px;\n\n      &:nth-child(1) {\n        background: md-get-palette-color(grey, 300);\n      }\n\n      &:nth-child(2) {\n        background: md-get-palette-color(grey, 400);\n      }\n\n      &:nth-child(3) {\n        background: md-get-palette-color(grey, 500);\n      }\n    }\n  }\n}\n","html":"<!-- import style from './columns.scss' -->\n<div :class=\"['md-layout', style.demo]\">\n  <div class=\"md-layout-item\"></div>\n  <div class=\"md-layout-item\"></div>\n  <div class=\"md-layout-item\"></div>\n</div>"}