export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './responsive.html';\n\nexport default class ExampleLayoutResponsive extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n@import '~jinge-material/style/animation/variables.scss';\n\n.md-layout-item {\n  height: 40px;\n  margin-top: 8px;\n  margin-bottom: 8px;\n  transition: 0.3s $md-transition-stand-timing;\n\n  &::after {\n    width: 100%;\n    height: 100%;\n    display: block;\n    background: md-get-palette-color(purple, 200);\n    content: ' ';\n  }\n}\n","html":"<!-- import style from './responsive.scss' -->\n<div :class=\"[style.demo, 'md-layout md-gutter md-alignment-center']\">\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n  <div class=\"md-layout-item md-medium-size-33 md-small-size-50 md-xsmall-size-100\"></div>\n</div>"}