export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './sizes.html';\n\nexport default class ExampleLayoutSizes extends Component {\n  static template = _tpl;\n}\n","sass":"@import '~jinge-material/style/palette.scss';\n\n.demo {\n  :global {\n    .md-layout-item {\n      height: 40px;\n\n      &::after {\n        width: 100%;\n        height: 100%;\n        display: block;\n        background: md-get-palette-color(green, 200);\n        content: ' ';\n      }\n    }\n  }\n}\n","html":"<!-- import style from './sizes.scss' -->\n<div :class=\"[style.demo, 'md-layout md-gutter']\">\n  <div class=\"md-layout-item md-size-15\"></div>\n  <div class=\"md-layout-item\"></div>\n  <div class=\"md-layout-item\"></div>\n  <div class=\"md-layout-item md-size-15\"></div>\n</div>"}